@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Joke-points {
  text-align: center;
  justify-content: space-around;
  width: 100px;
  display: inline-flex;
  flex-wrap: wrap;
}

.Joke td {
  padding: 5px 10px;
}

.Joke {
  border-bottom: 1px solid #556270;
}

.Joke td:last-child {
  font-size: 40px;
  color: #03389D;
}

.Joke button {
  background: none;
  border: none;
  margin: auto;
  font-size: 20px;
  padding: 5px;
  color: inherit;
  border-radius: 5px;
  -webkit-transition: 0.1s ease-in;
  transition: 0.1s ease-in;
}

.Joke button:hover {
  box-shadow: inset 0px 0px 50px 50px rgba(0,0,0,1);
  cursor: pointer;
  color: white;
}

.JokesList {
  font-family: 'Rubik', sans-serif;
  background: #FCFBE3;
  width: 75%;
  height: 90vh;
  margin: 50px auto;
  box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
}

.JokesList-header {
  color: white;
  background: #8733F0;
  width: 100%;
  height: 20%;
  margin: -2%;
  padding: 20px;
  box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
}

.JokesList-emoji {
  font-style: normal;
  font-size: 50px;
  content: '&#128514;';
}

.JokesList-title {
  margin: 0px;
  padding: 0px;
}

.subtitle {
  font-weight: 400;
}

.JokesList-list {
  margin: 3% auto 0 auto;
  /* text-align: left; */
  height: 70%;
  overflow-x: hidden;
  overflow-y: scroll;
}

.JokesList-list table {
  text-align: left;
  border-collapse: collapse;
}

::-webkit-scrollbar {
display: none;
}

.JokesList-list .btn {
  background: none;
  color: #8733F0;
  border: 2px solid #8733F0;
  border-radius: 10px;
  padding: 10px;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  margin: 20px;
}

.JokesList-list .btn:hover {
  color: white;
  cursor: pointer;
  box-shadow: inset 0px 0px 100px 100px #8733F0;
}

.JokesList-list .loader {
  margin: 20px;
  color: #8733F0;
}

